<template>
  <div id="app">
    <AppHeader/>
    <router-view/>
    <AppFooter/>
  </div>
</template>
<script>
import AppHeader from "@/components/AppHeader";
import AppFooter from "@/components/AppFooter";
import {mapActions} from "vuex";
export default {
  components: {
    AppHeader,
    AppFooter
  },
  methods: {
    ...mapActions(['headerUpdatePosition', 'headerUpdateHeight']),
    headerPos() {
      this.headerUpdatePosition()
    },
    heightHeader () {
      this.headerUpdateHeight()
    }
  },
  created () {
    window.addEventListener('scroll', this.headerPos);
  },
  mounted() {
    this.heightHeader ();
    document.querySelector("body").classList.remove('load')
  },
  destroyed () {
    window.removeEventListener('scroll', this.headerPos);
  }
}
</script>
<style lang="scss">
@import "public/static/style/style";
#app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
}
</style>
