export default {
    state: {
        header: false,
        headerPosition: false,
        headerHeight: 0
    },
    mutations: {
        changeHeaderState: (state) => {
            state.header = !state.header
            if (state.header === true) {
                document.body.style.overflow = 'hidden';
            } else {
                document.body.style.overflow = 'auto';
            }
        },
        changeHeaderTrue: (state) => {
            state.header = true
            if (state.header === true) {
                document.body.style.overflow = 'hidden';
            } else {
                document.body.style.overflow = 'auto';
            }
        },
        changeHeaderFalse: (state) => {
            state.header = false
            if (state.header === true) {
                document.body.style.overflow = 'hidden';
            } else {
                document.body.style.overflow = 'auto';
            }
        },
        getHeaderHeight: (state) => {
            state.headerHeight = document.querySelector('.header').scrollHeight;
        },
        getHeaderPosition: (state) => {
            state.headerPosition = window.pageYOffset > state.headerHeight ? true : false;
        },
    },
    actions: {
        toggleHeaderState ({commit}) {
            commit('changeHeaderState')
        },
        trueHeaderState ({commit}) {
            commit('changeHeaderTrue')
        },
        falseHeaderState ({commit}) {
            commit('changeHeaderFalse')
        },
        headerUpdatePosition ({commit}) {
            commit('getHeaderPosition')
        },
        headerUpdateHeight ({commit}) {
            commit('getHeaderHeight')
        }
    },
    getters: {
        headerState(state) {
            return state.header
        },
        headerPositionTop(state) {
            return state.headerPosition
        }
    },
}
